import { AppBar, Button, Card, Container, Divider, TextField, Toolbar, Typography } from "@mui/material";
import { ethers } from "ethers";
import React, { useEffect, useState } from "react";

import abi from "./util/Forum.json";

import "./App.css"

const contractForumAddress = "0x1314919a71963Ed41595896A382Aa684c677240A"
const contractForumABI = abi.abi

function App() {

  const [myAccount, setMyAccount] = useState(null);
  const [times, setTimes] = useState([]);
  const [senders, setSenders] = useState([]);
  const [messages, setMessages] = useState([]);
  const [number, setNumber] = useState(0);

  const [sendmsg, setSendmsg] = useState('')

  useEffect(() => {
    connectWalletAndGetAccount();
    return () => {
    }
  }, []);



  const {ethereum} = window;

  if (ethereum) {
    ethereum.on('accountsChanged', function (accounts) {
      setMyAccount(accounts[0])
    })
  }

  

  const connectWalletAndGetAccount = async () => {
    try {
      if (ethereum) {
        console.log('metamask is avaliable');
      } else {
        console.log('please install metamask');
      }

      const accounts = await ethereum.request({ method: "eth_requestAccounts"});

      console.log(accounts);

      if(accounts.length !== 0){
        let current_account = accounts[0];
        console.log('current account:', current_account);
        setMyAccount(current_account)
      } else {
        setMyAccount(null)
        alert("please install metamask or create an account");
      }
    }
    catch(err){
      console.log(err);
      setMyAccount(null)
      alert("please install metamask or create an account");
    }
  };

  const getMessages = async () => {
    const provider = new ethers.providers.Web3Provider(ethereum);
    const signer = provider.getSigner();
    const forumContract = new ethers.Contract(contractForumAddress, contractForumABI, signer);
    const messages = await forumContract.retrieve();
    console.log(messages);
    
    setTimes(messages[0])
    setSenders(messages[1])
    setMessages(messages[2])
  }


  const sendMessage = async () => {
    const provider = new ethers.providers.Web3Provider(ethereum);
    const signer = provider.getSigner();
    const forumContract = new ethers.Contract(contractForumAddress, contractForumABI, signer);

    try {
      let tx = await forumContract.store(sendmsg);
      setSendmsg('')
      await tx.wait();
    } catch (err) {
      console.log(err);
    } finally {
      getMessages();
    }
    
  }

  const updateSendMessage = event => {
    setSendmsg(event.target.value)
  }

  function getLocalTime(t) {
    return new Date(parseInt(t)*1000).toLocaleString()
  }


  return (
    <React.Fragment>
      <AppBar position="relative">
        <Toolbar>
          <Typography variant="h6" component="div">
            BlockForum
          </Typography>
        </Toolbar>
      </AppBar>
      {myAccount ? 
        <Container className="content-container">
          <Card variant="outlined" className="send-card card">
          
            <Typography 
              variant="subtitle2" 
              component="div"
              gutterBottom>
              {myAccount}
            </Typography>

            <TextField
              id="outlined-textarea"
              label=""
              multiline
              className="sendbox"
              onChange={updateSendMessage}
              value={sendmsg}
              rows={4}
            />

            <div className="sendbuttonbox">
              <Button 
                variant="contained"
                disabled={sendmsg.length===0} 
                onClick={sendMessage}>
                
              </Button>
            </div>

          </Card>

          <Button variant="contained" onClick={getMessages}>
            
          </Button>


          { 
            times.map((item, i) => {
                return (
                  <Card variant="outlined" className="card">
                    <div className="message-header">
                      <Typography 
                        variant="subtitle2" 
                        component="span"
                        gutterBottom>
                        #{senders.length-1-i}
                      </Typography>
                      
                      <Typography 
                        variant="subtitle2" 
                        component="span"
                        gutterBottom>
                        From:{senders[senders.length-1-i]}
                      </Typography>
        
                      <Typography 
                        variant="subtitle2" 
                        component="span"
                        gutterBottom>
                        :{getLocalTime(times[times.length-1-i].toNumber())}
                      </Typography>
                    </div>
        
                    <Divider></Divider>
                    
                    <Typography variant="body2" gutterBottom component="div">
                      {messages[messages.length-1-i]}
                    </Typography>
                </Card>
                )
            }) 
          }


          

        </Container>
        : 
        <Button onClick={connectWalletAndGetAccount}></Button>
      }
      
    </React.Fragment>
  );
}

export default App;
